* {
  box-sizing: border-box;
}

.App {
  height: 100%;
  width: 100vw;
  display: grid;
  grid-template-areas: "Navbar SearchBar SearchBar SearchBar"
                       "Navbar Main Main Main"
                       "Player Player Player Player";
  grid-template-columns: 3.5rem repeat(3, 2fr);
  grid-template-rows: 2.22rem 1fr 6.375rem;    
  background-color: #100e17;
  /* background-color: rgb(0, 5, 56); */
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

@media only screen and (max-width: 1400px) {
  html {    
    font-size: 17px;
  }
}

@media only screen and (max-width: 1200px) {
  html {    
    font-size: 16px;
  }
}

@media only screen and (max-width: 1000px) {
  html {    
    font-size: 15px;
  }
}


@media only screen and (max-width: 900px) {
  html {    
    font-size: 14px;
  }
}



.SearchBar {
  grid-area: SearchBar;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 2.223rem;
  width: 100%;  
  margin: auto;
  background-color: #17141d;
  /* background-color: rgba(0, 0, 0, 0); */
}

.SearchBar input {
  position: relative;
  width: 20rem;
  height: 2rem;
  padding: .30rem 0;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #010c3f;
  text-align: center;
  font-size: 1.2rem;
}

.SearchBar .Search_Button {
  position: relative;
  right: 1.6rem;
  color: rgb(0, 8, 33);
}

.Navigation-wrapper {
  grid-area: Navbar;
  height: 100%;
  background-color: #003809;
  /* background-color: rgb(0, 8, 33); */
}

.Mobile-Navigation {
  display: none;
}

.Navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 400px;
  margin-top: 2rem;
}

.Menu-Button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;  
  font-size: 25px;
}

.Menu-Button span {
  font-size: 12px;
}

a {
  text-decoration: none;
  text-align: left;
  color: rgba(255, 255, 255, 0.5);
  -webkit-transition: 300ms;
  transition: 300ms;
}

a:hover {
  color: rgb(255, 255, 255);
  -webkit-transform: scale(1.21);
          transform: scale(1.21);
}

.active {
  color: rgb(255, 255, 255);
}

@media only screen and (max-width: 1000px) {
  .Navigation span {
    display: none;
  }
}

.Playlist {
  -webkit-align-items: center;
          align-items: center;
  overflow-y: scroll;
  width: 90%;
  height: 100%;
  margin: 1rem auto;
  font-size: 16px;
  font-weight: 100;
}

.Playlist input {
  width: 100%;
  border: 0;
  outline: 0;
  background: transparent;
  font-family: 'Poppins', sans-serif;
  font-size: 1.5rem;
  color: #fff;
}


.TrackList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
}


.Track {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  cursor: pointer;
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  /* background-color: #100e17; */
  width: 50%;
  height: 100px;
}

.shadow {
  box-shadow: 0 24px 38px 3px rgba(0,0,0,0.50), 0 9px 46px 8px rgba(0,0,0,0.30), 0 11px 15px -7px rgba(0,0,0,0.70);
}

.Track:hover {
  box-shadow: 0 15px 40px 1px rgba(0,0,0,0.30), 0 12px 18px 1px rgba(0,0,0,0.30);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  z-index: 999;
}

.Track-information img {
  height: 3.333rem;
  margin: 0.5rem 1rem 0.5rem 0.33rem;
}

.Track-information {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.Track-name {
  -webkit-flex-grow: 4;
          flex-grow: 4;
}

.Track-information .Time {
