button.Button {
  cursor: pointer;
  padding: .40rem 0;
  color: rgba(255, 255, 255, 0.6);
  font-size: .833rem;
  float: right;
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

button.Button:hover {
  color: white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.Grid {
  display: grid;
  grid: repeat(auto-fit, minmax(-webkit-max-content, 20%)) / repeat(auto-fit, minmax(50%, auto));
  grid: repeat(auto-fit, minmax(max-content, 20%)) / repeat(auto-fit, minmax(50%, auto));
  position: relative;
  height: 100%;
  width: 95%;
  justify-items: center;
  margin: 1rem auto 0 auto;
}


@media only screen and (max-width: 1200px) {
  .Grid {
    grid: repeat(auto-fit, minmax(-webkit-max-content, 20%)) / repeat(auto-fit, minmax(50%, 100%));
    grid: repeat(auto-fit, minmax(max-content, 20%)) / repeat(auto-fit, minmax(50%, 100%));
  }
}

