.Action-Overlay {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  transition: opacity 0.3s linear;
}

.Action-Overlay:hover {
  opacity: 1;
}


.Action-Overlay .artist {
  border-radius: 50%;
}
